.container {
    display: grid;
    justify-content: center;
    align-content: center;
  }

.container img{
    /* Set rules to fill background */
    min-height: 100%;
    min-width: 1024px;
    
    /* Set up proportionate scaling */
    width: 100%;
    height: auto;
    
    /* Set up positioning */
    position: fixed;
    top: 0;
    left: 0;
}
.group-container{
  background-color: purple;
  position: relative;
  opacity: .9;
  border-radius: 30px;
  height: 90vh;
  width: 100vh;
  align-self: center;
  justify-content: center;
  text-align: center;
}

.group{
  display: grid;
  justify-content: center;
  position: relative;
  text-align: center;
}
